/*_app.min.css*/

.App {
    text-align: center
}
@media only screen and (max-width: 760px), (min-device-width: 768px) and (max-device-width: 1024px){
  body {
    overflow: scroll;
    width: auto;
    height: auto;
    text-overflow: ellipsis;
    font-size: 12px;
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #fff;
    zoom: -1%;
    padding-bottom: 35px;
  }
}
.
/**************/
.Data {
  padding: 24;
  background: '#fff';
  textAlign: 'center';
}
.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #fff
}

.fixed-top {
    display: fixed inline
}

.events {
    list-style: none;
    margin: 0;
    padding: 0
}

.events .ant-badge-status {
    overflow: hidden;
    white-space: nowrap;
    width: 100%;
    text-overflow: ellipsis;
    font-size: 12px
}

.notes-month {
    text-align: center;
    font-size: 28px
}

.notes-month section {
    font-size: 28px
}

.App-link {
    color: #61dafb
}
/*antd stuff*/
#components-layout-demo-side .logo {
    height: 32px;
